import { useState } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";

const MyNavbar = () => {
  const [collapse, setCollapse] = useState(false);

  return (
    <>
      <Navbar color="faded" light>
        <NavbarBrand href="/" className="mr-auto">
          Wappifu
        </NavbarBrand>
        <NavbarToggler
          onClick={() => setCollapse(!collapse)}
          className="mr-2"
        />
        <Collapse isOpen={collapse} navbar>
          <Nav navbar>
            <NavItem>
              <NavLink href="/waifus">Waifus</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/openings">Openings</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </>
  );
};

export default MyNavbar;
