import { Button, Modal, ModalBody, ModalHeader, FormGroup } from "reactstrap";
import Inputtext from "./Inputtext";
import { Formik, Form } from "formik";
import DatePicker from "./DatePicker";

const ModalEditWaifu = ({ openModal, setOpenModal, handleUpdate, toEdit }) => {
  const updateFunction = (values) => {
    handleUpdate(values);
    setOpenModal(!openModal);
  };

  return (
    <Formik
      initialValues={{
        id: toEdit.id,
        name: toEdit.name,
        lastname: toEdit.lastname,
        day: toEdit.day,
        month: toEdit.month,
        anime: toEdit.anime,
        seiyuuname: toEdit.seiyuuname,
        seiyuulastname: toEdit.seiyuulastname,
      }}
      onSubmit={(values) => updateFunction(values)}
    >
      <Modal isOpen={openModal}>
        <ModalHeader>Edit Waifu Info</ModalHeader>
        <Form>
          <ModalBody>
            <FormGroup>
              <Inputtext name={"name"} label={"Name"} />
              <Inputtext name={"lastname"} label={"Lastname"} />
            </FormGroup>
            <FormGroup>
              <DatePicker name={"day"} label={"Day"} />
              <DatePicker name={"month"} label={"Month"} />
            </FormGroup>
            <FormGroup>
              <Inputtext name={"anime"} label={"Anime"} />
            </FormGroup>
            <FormGroup>
              <Inputtext name={"seiyuuname"} label={"Seiyuu Name"} />
              <Inputtext name={"seiyuulastname"} label={"Seiyuu Lastname"} />
            </FormGroup>

            <Button color={"success"} type={"submit"}>
              Confirm
            </Button>
            <Button color={"danger"} onClick={() => setOpenModal(!openModal)}>
              Cancel
            </Button>
          </ModalBody>
        </Form>
      </Modal>
    </Formik>
  );
};

export default ModalEditWaifu;
