import MyNavbar from "./MyNavBar";
import { Container, Collapse } from "reactstrap";
import WaifuForm from "./WaifuForm";
import { useEffect, useState } from "react";
import WaifuList from "./WaifuList";
import ModalEditWaifu from "./ModalEditWaifu";
import waifuService from "../services/Waifu";

const Waifus = () => {
  const [openWaifu, setOpenWaifu] = useState(false);
  const [toEdit, setToEdit] = useState({});
  const [openModal, setOpenModal] = useState(false);
  const [waifus, setWaifus] = useState([]);

  useEffect(() => {
    waifuService.getAll().then((res) => {
      setWaifus(res);
    });
  }, []);

  const handleSubmit = (values, resetForm) => {
    const waifuToAdd = {
      id: waifus.length + 1,
      name: values.name.toLowerCase(),
      lastname: values.lastname.toLowerCase(),
      day: values.day,
      month: values.month,
      anime: values.anime.toLowerCase(),
      seiyuuname: values.seiyuuname.toLowerCase(),
      seiyuulastname: values.seiyuulastname.toLowerCase(),
    };

    waifuService.addWaifu(waifuToAdd).then((returned) => {
      setWaifus(waifus.concat(returned));
      setOpenWaifu(!openWaifu);
      resetForm();
    });
  };

  const handleUpdate = (toUpdate) => {
    console.log(waifus);
    waifuService.updateWaifu(toUpdate.id, toUpdate).then((response) => {
      setWaifus(waifus.map((el) => (el.id !== toUpdate.id ? el : response)));
    });
  };

  const handleDelete = (toDelete) => {
    if (
      window.confirm(
        `Are you sure that ${
          toDelete.name + " " + toDelete.lastname
        } will stop being your wife?`
      )
    ) {
      const copy = [...waifus];
      const replaceList = copy.filter(
        (wife) => wife.name.toLowerCase() !== toDelete.name.toLowerCase()
      );
      waifuService.deleteWaifu(toDelete.id).then(() => {
        setWaifus(replaceList);
      });
    }
  };
  const formatString = (str) => {
    if (str === undefined) {
      return str;
    } else {
      const formatedStr = str.charAt(0).toUpperCase() + str.slice(1);
      return formatedStr;
    }
  };

  return (
    <>
      <MyNavbar />
      <Container>
        <WaifuList
          waifus={waifus}
          handleDelete={handleDelete}
          handleUpdate={handleUpdate}
          openWaifu={openWaifu}
          setOpenWaifu={setOpenWaifu}
          toEdit={toEdit}
          setToEdit={setToEdit}
          openModal={openModal}
          setOpenModal={setOpenModal}
          formatString={formatString}
        />
        <Collapse isOpen={openWaifu}>
          <WaifuForm
            waifus={waifus}
            setWaifus={setWaifus}
            openWaifu={openWaifu}
            setOpenWaifu={setOpenWaifu}
            handleSubmit={handleSubmit}
          />
        </Collapse>
        {openModal === false ? null : (
          <ModalEditWaifu
            openModal={openModal}
            setOpenModal={setOpenModal}
            handleUpdate={handleUpdate}
            toEdit={toEdit}
          />
        )}
      </Container>
    </>
  );
};

export default Waifus;
