import { Formik, Form } from "formik";
import { Button } from "reactstrap";
import Inputtext from "./Inputtext";
import DatePicker from "./DatePicker";
import validate from "../services/validate";

const WaifuForm = ({ handleSubmit }) => {
  return (
    <>
      <Formik
        initialValues={{
          name: "",
          lastname: "",
          day: "",
          month: "",
          anime: "",
          seiyuuname: "",
          seiyuulastname: "",
        }}
        validate={validate}
        onSubmit={(values, { resetForm }) => handleSubmit(values, resetForm)}
      >
        <Form>
          <Inputtext name={"name"} label={"Name"} />
          <Inputtext name={"lastname"} label={"Lastname"} />
          <DatePicker name={"day"} label={"Day"} />
          <DatePicker name={"month"} label={"Month"} />
          <Inputtext name={"anime"} label={"Anime"} />
          <Inputtext name={"seiyuuname"} label={"Seiyuu Name"} />
          <Inputtext name={"seiyuulastname"} label={"Seiyuu Lastname"} />
          <Button color="primary" type="submit">
            Submit
          </Button>
        </Form>
      </Formik>
    </>
  );
};

export default WaifuForm;
