import ButtonCollapse from "./ButtonCollapse";
import { Pencil } from "react-bootstrap-icons";

const WaifuDetails = ({
  waifu,
  formatString,
  openModal,
  setOpenModal,
  setToEdit,
}) => {
  const setEdit = () => {
    setToEdit(waifu);
    setOpenModal(!openModal);
  };
  return (
    <>
      Anime: {formatString(waifu.anime)}
      <br />
      Seiyuu:{" "}
      {formatString(waifu.seiyuuname) +
        " " +
        formatString(waifu.seiyuulastname)}
      <br />
      Birthday: {waifu.day + " / " + waifu.month}
      <br />
      <ButtonCollapse color={"warning"} onClick={setEdit}>
        {<Pencil />}
      </ButtonCollapse>
    </>
  );
};
export default WaifuDetails;
