const validate = (values) => {
  const error = {};

  if (!values.name) {
    error.name = "Waifu name is required";
  } else if (values.name.length < 2) {
    error.name = "Waifu name too is short";
  }

  if (!values.lastname) {
    error.lastname = "Waifu lastname is required";
  } else if (values.lastname.length < 2) {
    error.lastname = "Waifu lastname too is short";
  }

  if (!values.anime) {
    error.anime = "Anime name is required";
  } else if (values.lastname.length < 2) {
    error.anime = "Anime name too is short";
  }

  if (!values.day || values.day === "") {
    error.day = "Waifu birth day is required";
  }

  if (!values.month || values.month === "") {
    error.month = "Waifu birth month is required";
  }

  if (!values.seiyuuname) {
    error.seiyuuname = "Seiyuu name is required";
  } else if (values.seiyuuname.length < 2) {
    error.seiyuuname = "Seiyuu name is too short";
  }

  if (!values.seiyuulastname) {
    error.seiyuulastname = "Seiyuu lastname is required";
  } else if (values.seiyuulastname.length < 2) {
    error.seiyuulastname = "Seiyuu lastname too is short";
  }

  return error;
};

export default validate;
