import axios from "axios";

const baseUrl = "./api/waifus";

const getAll = () => {
  const request = axios.get(baseUrl);
  return request.then((response) => response.data);
};

const deleteWaifu = (id) => {
  const request = axios.delete(`${baseUrl}/${id}`);
  return request.then((response) => response.data);
};

const addWaifu = (waifu) => {
  const request = axios.post(baseUrl, waifu);
  return request.then((response) => response.data);
};

const updateWaifu = (id, newWaifu) => {
  const request = axios.put(`${baseUrl}/${id}`, newWaifu);
  return request.then((res) => res.data);
};

export default { getAll, deleteWaifu, addWaifu, updateWaifu };
