import { Button, Table } from "reactstrap";
import FlexDiv from "./FlexDiv";
import Waifu from "./Waifu";

const WaifuList = ({
  waifus,
  handleDelete,
  handleUpdate,
  openWaifu,
  setOpenWaifu,
  setToEdit,
  openModal,
  setOpenModal,
  formatString,
}) => {
  return (
    <>
      <Table>
        <thead>
          <tr>
            <th>ID</th>
            <th>Your Waifus</th>
            <th>
              <FlexDiv>
                <p>Add</p>
                <Button
                  color={openWaifu === true ? "danger" : "success"}
                  onClick={() => setOpenWaifu(!openWaifu)}
                >
                  {openWaifu === true ? "-" : "+"}
                </Button>
              </FlexDiv>
            </th>
          </tr>
        </thead>
        <tbody>
          {waifus.map((waifu, i) => {
            return (
              <tr key={i}>
                <Waifu
                  waifu={waifu}
                  handleDelete={handleDelete}
                  handleUpdate={handleUpdate}
                  openModal={openModal}
                  setOpenModal={setOpenModal}
                  setToEdit={setToEdit}
                  formatString={formatString}
                />
              </tr>
            );
          })}
        </tbody>
      </Table>
    </>
  );
};

export default WaifuList;
