import { useState } from "react";
import { Collapse } from "reactstrap";
import ButtonCollapse from "./ButtonCollapse";
import WaifuDetails from "./WaifuDetails";

const Waifu = ({
  waifu,
  handleDelete,
  handleUpdate,
  openModal,
  setOpenModal,
  setToEdit,
  formatString,
}) => {
  const [openDetails, setOpenDetails] = useState(false);
  return (
    <>
      <th scope="row">{waifu.id}</th>
      <td>
        {formatString(waifu.name)} {formatString(waifu.lastname)}
        <Collapse isOpen={openDetails}>
          <WaifuDetails
            waifu={waifu}
            formatString={formatString}
            handleUpdate={handleUpdate}
            openModal={openModal}
            setOpenModal={setOpenModal}
            setToEdit={setToEdit}
          />
        </Collapse>
      </td>
      <td>
        <ButtonCollapse
          color={openDetails === true ? "danger" : "success"}
          onClick={() => setOpenDetails(!openDetails)}
        >
          {openDetails === true ? "Less" : "More"}
        </ButtonCollapse>
        <ButtonCollapse color={"danger"} onClick={() => handleDelete(waifu)}>
          X
        </ButtonCollapse>
      </td>
    </>
  );
};

export default Waifu;
